import React from 'react';
import { useNavigate } from 'react-router-dom';
import './menu.css'; 

const MenuPlanificador = () => {
  const navigate = useNavigate();

  return (
    <div className="roles-container">
      <div className="nav-container">
        <button className="nav-btn" onClick={() => navigate(-1)}>
           Atrs
        </button>
        <button className="nav-btn" onClick={() => navigate('/')}> 
           Inicio
        </button>
      </div>
      <div className="roles-card">
        <h2 className="title">Gestin de la Programacin Semanal</h2>
        <div className="button-container">
          <button 
            className="primary-btn" 
            onClick={() => navigate('/programacion/anadir')}
          >
            Aadir Programacin
          </button>
          <button 
            className="primary-btn" 
            onClick={() => navigate('/planificador/editar-programacion')}
          >
            Editar Programacin
          </button>
          <button 
            className="primary-btn" 
            onClick={() => navigate('/planificador/consultar-programacion')}
          >
            Consultar Programacin
          </button>
          <button 
            className="primary-btn" 
            onClick={() => navigate('/planificador/eliminar-programacion')}
          >
            Eliminar Programacin
          </button>
        </div>
      </div>
    </div>
  );
};

export default MenuPlanificador;